    API_DATA(HID, date, TRID) {
        ApiProvider.getStoredProcedureRESSECT(HID, 1, "ALL", "DOC3924B")
            .then(data => {
                const ReportList = data.data
                const mapList = ReportList.filter(function (value, index, array) {
                    const DTstring = value.ORRCPDT.toString()
                    const TMstring = value.ORRCPTM.toString()
                    const time = DTstring + TMstring
                    return moment(time, 'YYYY-MM-DDHH:mm:ss').valueOf() > date.valueOf()
                })
                this.setState({ report: mapList.length })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureRESSECT NG " + JSON.stringify(err));
            });
        ApiProvider.getStoredProcedureORDLIST(HID, date.format('YYYY-MM-DD'), "ALL", "DOC3924B")
            .then(data => {
                const filter = data.data.filter(function (info, index, arr) {
                    return info.ORSTATUS === "31"
                })
                this.setState({ nonreport: filter.length })
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err));
            });
        ApiProvider.getOnePatientRemark(TRID)
            .then(data => {
                this.setState({
                    remark: data.data[0].context
                })
            })
            .catch(err => {
                console.log("Andy test getOnePatientRemark NG " + JSON.stringify(err));
            })
        ApiProvider.getPersionalStatus(HID)
            .then(data => { console.log(data.data); this.setState({ persionstatus: data.data }) })
            .catch(err => { console.log(err) })
    }

