import React, { Component } from 'react';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'

class RTtable extends Component {
    state = {
        RESDBGAS: [],
        PreHID: null,
        PreTransInDate: null,
    }
    componentDidMount() {
        const HID = this.props.caseinfo.HISID
        const TRDT = this.props.caseinfo.TRANSINDT
        this.setState({ PreHID: HID, PreTransInDate: TRDT })
        this.getGasAPI()
    }
    componentDidUpdate() {
        const HID = this.props.caseinfo.HISID
        const TRDT = this.props.caseinfo.TRANSINDT
        const PreHID = this.state.PreHID
        const PreTRDT = this.state.PreTransInDate
        if (HID !== PreHID || TRDT !== PreTRDT) {
            this.setState({ PreHID: HID, PreTransInDate: TRDT })
            this.getGasAPI()
        }
    }
    getGasAPI() {
        const { caseinfo } = this.props
        const HID = caseinfo.HISID
        const TransInDate = moment(caseinfo.TRANSINDT, "YYYYMMDD").format("YYYY-MM-DD")
        ApiProvider.getStoredProcedureRESDBGAS(HID, TransInDate)
            .then(data => { this.setState({ RESDBGAS: data.data }) })
            .catch(err => {
                console.log("getStoredProcedureRESDBGAS NG " + JSON.stringify(err));
            });
    }
    GasListToCell = () => {
        function formatGasData(GasDataList, st, et) {
            let filterTimer = GasDataList.filter(function (element, index, arr) {
                const timeStamp = moment(element.ORRESDT.toString().trim() + element.ORRESTM.toString().trim(), 'YYYY-MM-DDHH.mm.ss').valueOf()
                return st <= timeStamp && timeStamp <= et
            })

            filterTimer = filterTimer.sort((a, b) => {
                let tempTimeA = moment(a.ORRESDT.toString().trim(), 'YYYY-MM-DD').valueOf()
                let tempTimeB = moment(b.ORRESDT.toString().trim(), 'YYYY-MM-DD').valueOf()
                return tempTimeA < tempTimeB ? 1 : -1
            })

            const decideStyle = {
                "1": { "min": 6.5, "max": 7.7 },
                "2": { "min": 0, "max": 100 },
                "3": { "min": 0, "max": 200 },
                "4": { "min": 0, "max": 40 },
                "5": { "min": -30, "max": 20 },
                "6": { "min": 0, "max": 100 },
                "7": { "min": 0, "max": 100 }
            }

            const cellMap = filterTimer.map(function (element, index, arr) {
                const PHValue = element.SS07
                const PCO2Value = element.SS06
                const PO2Value = element.SS08
                const HCO3Value = element.SS04
                const BEValue = element.SS01
                const O2SatValue = element.SS05
                const FiO2Value = element.SS03
                const timeStamp = moment(element.ORRESDT.toString().trim() + element.ORRESTM.toString().trim(), 'YYYY-MM-DDHH.mm.ss').valueOf()
                const time = moment(timeStamp).format("MM/DD HH:mm")
                const gasDataArray = [time, PHValue, PCO2Value, PO2Value, HCO3Value, BEValue, O2SatValue, FiO2Value]
                const GasCell = gasDataArray.map(function (value, index, array) {
                    let tempStyle = false
                    const tempValue = parseFloat(value)

                    if (index != 0 && (tempValue > decideStyle[index.toString()].max || tempValue < decideStyle[index.toString()].min) || isNaN(tempValue) == true) {
                        tempStyle = true;
                    }

                    return (
                        <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                            <t3 style={{ color: tempStyle ? "red" : "black", fontWeight: 600 }}>{value}</t3>
                        </centerstyle>
                    );
                })
                return (
                    <div key={index} style={{ height: "50px", display: "grid", gridTemplateColumns: "110px repeat(7,1fr)" }}>{GasCell}</div>
                );
            })
            return cellMap

        }
        const { Timer } = this.props
        const startTime = Timer[0]
        const endTime = Timer[1]
        const GasData = this.state.RESDBGAS
        const CellRander = formatGasData(GasData, startTime, endTime)
        //for loop filter three days

        return CellRander
    }

    render() {
        const GasTitleList = ['æé', 'pH', `PCO`, 'PO', 'HCO', 'BE', 'O2SAT', 'FiO2']
        const GasTitleCell = GasTitleList.map(function (value, index, array) {
            let temp = value == 'PCO' || value == 'PO' || value == 'HCO' ? true : false
            return (
                <centerstyle key={index} style={{ borderCollapse: "collapse" }}>
                    <t3>{value}</t3>
                    {temp && <sub>2</sub>}
                </centerstyle>
            );
        })

        return (
            <div>
                <div style={{ height: "50px", border: "rgba(238,238,238,1) 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "110px repeat(7,1fr)" }}>
                    {GasTitleCell}
                </div>
                <div style={{ maxHeight: "400px", overflow: "auto" }}>
                    {this.GasListToCell()}
                </div>
            </div>
        );
    }
}

export default RTtable
